import React from 'react';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Divider from '@material-ui/core/Divider';
import CheckIcon from '@material-ui/icons/Check';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import logo1 from '../../assets/images/education/1.png';
import logo2 from '../../assets/images/education/2.png';
import logo3 from '../../assets/images/education/3.jpg';

import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        toRight: {
            marginLeft: 'auto'
        },
        courseList: {
            width: '100%',
            maxWidth: 360,
            backgroundColor: theme.palette.background.paper,
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        }
    }),
);

const logos = [
    logo1, logo2, logo3
];

export interface School {
    id: number;
    institution: string;
    website: string;
    area: string;
    studyType: string;
    start: { year: number, month?: number };
    end: { year: number, month?: number };
    description: string;
}

export interface SchoolList {
    list: Array<School>;
    courses: Array<String>;
}

const Education: React.FC<SchoolList> = ({ list, courses }): JSX.Element => {

    const classes = useStyles();

    return (
        <React.Fragment>
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                {list.map((school, index) => (
                    <LazyLoad once key={school.id} placeholder={<Loader />}>
                        <Box p={1} flexGrow={1}>
                            <Card>
                                <CardHeader avatar={<Avatar
                                    variant="rounded"
                                    className={classes.medium}
                                    alt={school.institution}
                                    src={logos[index]} />}
                                    title={school.institution}
                                    subheader={school.area} />
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.description}
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.studyType}
                                    </Typography>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {school.start.year} - {school.end.year}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button
                                        className={classes.toRight}
                                        href={school.website}
                                        target="_blank"
                                        size="small"
                                        variant="outlined">Institution Website</Button>
                                </CardActions>
                            </Card>
                        </Box>
                    </LazyLoad>
                ))}
            </Box>
            <Divider />
            <p></p>
            <Typography variant="body2" color="textPrimary" component="h1">Featured courses that I took and enjoyed.</Typography>
            <Box display="flex" flexWrap="wrap" justifyContent="center" p={1} m={1}>
                <List component="nav" className={classes.courseList} aria-label="mailbox folders">
                    {courses.map((course, index) => (
                        <React.Fragment key={index}>
                            <ListItem button>
                                <ListItemIcon>
                                    <CheckIcon />
                                </ListItemIcon>
                                <ListItemText primary={course} />
                            </ListItem>
                            <Divider />
                        </React.Fragment>
                    ))}
                </List>
            </Box>
        </React.Fragment>
    );
}

export default Education;