import React from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import logo1 from '../../assets/images/skills/1.png';
import logo2 from '../../assets/images/skills/2.png';
import logo3 from '../../assets/images/skills/3.png';
import logo4 from '../../assets/images/skills/4.png';
import logo5 from '../../assets/images/skills/5.png';
import logo6 from '../../assets/images/skills/6.png';
import logo7 from '../../assets/images/skills/7.png';
import logo8 from '../../assets/images/skills/8.png';
import logo9 from '../../assets/images/skills/9.png';

import Avatar from '@material-ui/core/Avatar';

import Rating from '@material-ui/lab/Rating';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        },
    }),
);

export interface Skill {
    id: number;
    name: string;
    value: number;
    knownSince: number,
    yearsOfWorkExperience: number;
    notes: string;
}

export interface SkillsList {
    list: Array<Skill>;
}

const logos = [
    logo1, logo2, logo3, logo4, logo5, logo6, logo7, logo8, logo9
];

const Skills: React.FC<SkillsList> = ({ list }): JSX.Element => {
    const classes = useStyles();
    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Skills</Typography>
            </Box>
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                {list.map((skill, index) => (
                    <Box p={1} key={skill.id} flexGrow={1}>
                        <Card>
                            <CardHeader avatar={<LazyLoad once placeholder={<Loader />}><Avatar
                                variant="rounded"
                                className={classes.medium}
                                alt={skill.name}
                                src={logos[index]} /></LazyLoad>}
                                title={skill.name} subheader={"known since " + skill.knownSince} />
                            <CardContent>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {skill.yearsOfWorkExperience}-year(s) professional work experience
                                </Typography>
                            </CardContent>
                            <CardActions>
                                <Rating name="read-only" value={skill.value} precision={0.5} readOnly />
                            </CardActions>
                        </Card>
                    </Box>
                ))}
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <p>Under construction...</p>
            </Box>            
        </React.Fragment>
    );
};

export default Skills;