import React from 'react';

import { Box } from '@material-ui/core'

import Typist from 'react-typist';
import { Typography } from '@material-ui/core';

import BackgroundSlider from 'react-background-slider';
import image1 from '../../assets/images/image1.jpg';
import image2 from '../../assets/images/image2.png';

import './Home.css';

const Home: React.FC = (): JSX.Element => {

    const lines = [
        'Hüseyin Deniz KIVRAK',
        ' ',
        'Senior Software Engineer',
        ' ',
        'Creating applications to help people improve productivity in their workflow.'
    ];

    const bgImages = [image1, image2];

    return (
        <Box width={'80%'} m="auto" mt={2} p={2} textAlign="center" borderRadius="10px" color="grey" bgcolor="rgba(255,255,255,0.5)">
            <Typist startDelay={2000} cursor={{ hideWhenDone: true }}>
                {lines.map((text, index) => { return (<Typography variant="h5" component="span" key={index}>{text}<Typist.Delay ms={1250} /><br /></Typography>) })}
            </Typist>
            <BackgroundSlider images={bgImages} duration={5} transition={2} />
        </Box>
    );
};

export default Home;