import React from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { Box } from '@material-ui/core'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        large: {
            width: theme.spacing(20),
            height: theme.spacing(20),
        },
    }),
);


const AboutMe: React.FC = (): JSX.Element => {
    const classes = useStyles();
    return (
        <Typography component="div">
            <Box width={'80%'} m="auto" mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Summary</Typography>
                <Box m={1}>
                    I am a software engineer with 10+ years experience, and I am seeking new opportunities in the field.
                </Box>
                <Box m={1}>
                    I am a result oriented and self-motivated professional.
                </Box>
                <Box m={1}>
                    First, I worked as a part-time instructor in various computer courses in Ankara, Turkey before 2010. Additionally, I created/contributed 10+ enterprise applications for companies/government agencies in Turkey in between the years 2010-2017. Finally, I have managed 5+ engineers for robotics department of a logistics/technology company in Turkey for 3 years.
                </Box>
                <Box m={1}>
                    I am skilled in object-oriented programming languages, software architecture and backend/frontend frameworks/libraries for web applications.
                </Box>
                <Box display="flex" m="auto" mt={3} mb={3} justifyContent="center" alignContent="center" alignItems="center">
                    <Avatar alt="Hüseyin Deniz KIVRAK" src="https://avatars1.githubusercontent.com/u/1695352?v=4" className={classes.large} />
                </Box>
            </Box>
        </Typography>
    )
};

export default AboutMe;