import React, { useEffect, useState } from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';

import logo1 from '../../assets/images/experience/1.jpg';
import logo2 from '../../assets/images/experience/2.png';
import logo3 from '../../assets/images/experience/3.png';
import logo4 from '../../assets/images/experience/4.png';
import logo5 from '../../assets/images/experience/5.jpg';

import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        toRight: {
            marginLeft: 'auto'
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        }
    }),
);

export interface Work {
    id: number;
    company: string;
    position: string;
    website: string;
    location: string;
    summary: string;
    isCurrentRole: boolean;
    start: { year: number, month?: number };
    end: { year: number, month?: number };
}

export interface WorkList {
    list: Array<Work>;
}

const logos = [
    logo1, logo2, logo3, logo4, logo5
];

const Experience: React.FC<WorkList> = ({ list }): JSX.Element => {

    const classes = useStyles();

    const [workList, setWorkList] = useState<Work[]>([]);

    useEffect(() => {
        //const timer = setTimeout(() => {
        setWorkList(list);
        //    clearTimeout(timer);
        //}, 2000);
    }, [list]);

    if (workList.length > 0) {
        return (
            <React.Fragment>
                <Box mt={2}>
                    <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Experience</Typography>
                </Box>
                <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                    {workList.map((work, index) => (
                        <LazyLoad once key={work.id} placeholder={<Loader />}>
                            <Box p={1} flexGrow={1}>
                                <Card>
                                    <CardHeader
                                        avatar={<Avatar
                                            variant="rounded"
                                            className={classes.medium}
                                            alt={work.company}
                                            src={logos[index]} />}
                                        title={work.position}
                                        subheader={work.company + " / " + work.location}
                                        action={work.start.month + "-" + work.start.year + " / " + work.end.month + "-" + work.end.year} />
                                    <CardContent>
                                        <Typography variant="body2" color="textPrimary" component="p">
                                            {work.summary}
                                        </Typography>
                                    </CardContent>
                                    <CardActions>
                                        <Button
                                            className={classes.toRight}
                                            href={work.website}
                                            target="_blank"
                                            size="small"
                                            variant="outlined">Company Website</Button>
                                    </CardActions>
                                </Card>
                            </Box>
                        </LazyLoad>
                    ))}
                </Box>
            </React.Fragment>
        );
    }
    else {
        return (
            <Loader />
        );
    }

};

export default Experience;