import React from 'react';

import LazyLoad from 'react-lazyload';
import Loader from '../../components/Loader/Loader';

import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';

import logo1 from '../../assets/images/projects/1.jpg';
import logo2 from '../../assets/images/projects/2.png';

import Avatar from '@material-ui/core/Avatar';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        media: {
            backgroundSize: 'contain',
            height: 0,
            paddingTop: '40%', // 16:9
        },
        toRight: {
            marginLeft: 'auto'
        },
        medium: {
            width: theme.spacing(10),
            height: theme.spacing(10),
        }
    }),
);

export interface ProjectImage {
    thumbnail: string;
    mobile: string;
    desktop: string;
}

export interface Project {
    id: number;
    name: string;
    summary: string;
    website: string;
    languages: Array<string>;
    libraries: Array<string>;
    images: Array<ProjectImage>;
}

export interface ProjectList {
    list: Array<Project>;
}

const logos = [
    logo1, logo2
];

const Projects: React.FC<ProjectList> = ({ list }): JSX.Element => {

    const classes = useStyles();

    return (
        <React.Fragment>
            <Box mt={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Featured Projects</Typography>
            </Box>
            <Box display="flex" flexWrap="wrap" justifyContent="flex-start" p={1} m={1}>
                {list.map((project, index) => (
                    <LazyLoad once key={project.id} placeholder={<Loader />}>
                        <Box p={1} flexGrow={1}>
                            <Card>
                                <CardHeader
                                    avatar={<Avatar
                                        variant="rounded"
                                        className={classes.medium}
                                        alt={project.name}
                                        src={logos[index]} />}
                                    title={project.name}
                                    action={project.languages[0]} />
                                <CardMedia
                                    className={classes.media}
                                    image={project.images[0].thumbnail}
                                    title={project.name} />
                                <CardContent>
                                    <Typography variant="body2" color="textPrimary" component="p">
                                        {project.summary}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button
                                        className={classes.toRight}
                                        href={project.website}
                                        target="_blank"
                                        size="small"
                                        variant="outlined">Project Website</Button>
                                </CardActions>
                            </Card>
                        </Box>
                    </LazyLoad>
                ))}
            </Box>
            <Box mt={2} pb={2}>
                <Typography variant="subtitle1" color="textPrimary" component="h2">Others</Typography>
                <p>Under construction...</p>
            </Box>             
        </React.Fragment>
    );
};

export default Projects;