import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import { Box } from '@material-ui/core'
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GithubIcon from '@material-ui/icons/GitHub';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        large: {
            width: theme.spacing(20),
            height: theme.spacing(20),
        },
    }),
);


const Contact: React.FC = (): JSX.Element => {
    const classes = useStyles();
    return (
        <React.Fragment>
            <Box display="flex" width={'80%'} m="auto" mt={2} justifyContent="center" alignContent="center" alignItems="center">
                <Avatar alt="Hüseyin Deniz KIVRAK" src="https://avatars1.githubusercontent.com/u/1695352?v=4" className={classes.large} />
            </Box>
            <Box display="flex" flexWrap="wrap" justifyContent="center" p={1} m={1}>
                <Box p={1}>
                    <Button
                        variant="outlined"
                        href="https://tr.linkedin.com/in/huseyindenizkivrak"
                        target="_blank"
                        startIcon={<LinkedInIcon fontSize="large" />}
                        size="large">
                        huseyindenizkivrak
                </Button>
                </Box>
                <Box p={1}>
                    <Button
                        variant="outlined"
                        href="https://github.com/huseyindeniz"
                        target="_blank"
                        startIcon={<GithubIcon fontSize="large" />}
                        size="large">
                        huseyindeniz
                </Button>
                </Box>
            </Box>
        </React.Fragment>
    )
};

export default Contact;